import ReactDOM from "react-dom";
import React from "react";

import {
    AdaptivityProvider,
    AppRoot, Button,
    Card,
    CardScroll,
    ConfigProvider,
    Link, Tabs, TabsItem,
    Title,
    useAdaptivity, ViewWidth, Tappable, HorizontalScroll, Input, Text,
} from "@vkontakte/vkui";
import "@vkontakte/vkui/dist/vkui.css";

import {Template} from "../Template/Template";

import brands_data from './data/profiles_20210920.json';
import filters from './data/filters_20210920.json';


function BrandForm(props) {
    const { viewWidth } = useAdaptivity();
    const [value, setValue] = React.useState("");
    const handleChange = (event) => {
        setValue(event.target.value)
    }

    const handleSubmit = (e) => {
        e.preventDefault();
        const requestOptions = {
            method: 'POST',
            headers: {
                'Authorization': 'Bearer keyVWFbLtlqU1tMht',
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                "fields": {
                    "brand": value,
                }
            })
        };

        fetch("https://api.airtable.com/v0/appPsBMka6L5vpHXI/" + props.table, requestOptions).then(res => setValue("!  !"));
    }

    return (
        <form onSubmit={handleSubmit} style={{ display: viewWidth > ViewWidth.MOBILE ? "flex" : "inherit", marginBottom: 30, gap: 10, alignItems: 'baseline', minWidth: 0, overflow: 'hidden'}}>
            <Title level="4" weight="regular" style={{marginBottom: 10}}>
                {props.text}
            </Title>
            <Input onChange={handleChange} value={value} placeholder={props.placeholder} type="text" style={{width: 300, marginBottom: 10}}/>
            <Button mode="outline" type="submit" size='m'></Button>
        </form>
    )
}

function ImageCard(props) {
    const local_url = "public/brands/" + props.post.local_url;
    const { viewWidth } = useAdaptivity();

    const handleClick = () => {
        const requestOptions = {
            method: 'POST',
            headers: {
                'Authorization': 'Bearer keyVWFbLtlqU1tMht',
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                "fields": {
                    "brand": props.name,
                    "url": props.post.shortcode,
                }
            })
        };
        fetch("https://api.airtable.com/v0/appPsBMka6L5vpHXI/topbrands", requestOptions);
    }

    return (
        <Card style={{width: viewWidth > ViewWidth.MOBILE ? '30%' : '70%', aspectRatio: '4/5'}}>
            <Tappable hasActive={false} hoverMode="opacity" Component="a" onClick={handleClick} href={props.post.shortcode} target="_blank" style={{ borderRadius: 'inherit', width: '100%', height: '100%', display: 'block' }}>
                <img
                    src={local_url}
                    alt={props.name}
                    style={{width: '100%', height: '100%', objectFit: 'cover', borderRadius: 'inherit'}}
                />
            </Tappable>
        </Card>
    )
}

function Brand(props) {
    const name = props.brand.name;

    return (
        <React.Fragment>
            <div style={{ display:'flex', marginTop:30, marginBottom: 10, alignItems: 'baseline', minWidth: 0, overflow: 'hidden' }}>
                <Title level="2" weight="regular" title={name} style={{
                    marginRight: 8,
                    flexShrink: 1,
                    overflow: 'hidden',
                    textOverflow: 'ellipsis',
                    whiteSpace: 'nowrap',
                }}>
                    {name}
                    {/*<br/> Followers: {props.followers}*/}
                </Title>
                <Link style={{flexShrink: 0}} href={"https://www.instagram.com/" + props.brand_name}>{"@" + props.brand_name}</Link>
            </div>
            <CardScroll size="s" style={{marginRight: -20, marginLeft: -20, marginBottom: 40}}>
                {props.brand.posts.map((item) => <ImageCard post={item} name={name}/>)}
            </CardScroll>
        </React.Fragment>
    )
}

function Filters(props) {
    const tabs = {'all': 'Bce', 'creative': '', 'base': '', 'men': '', 'accessories': ''};
    const [activeTab, setActiveTab] = React.useState('all');

    const handleClick = (active_tab) => {
        setActiveTab(active_tab)
        if (active_tab in filters) {
            props.setBrands(filters[active_tab]);
        } else {
            props.setBrands(props.allBrands.sort(() => Math.random() - 0.5));
        }

        const requestOptions = {
            method: 'POST',
            headers: {
                'Authorization': 'Bearer keyVWFbLtlqU1tMht',
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                "fields": {
                    "tab": active_tab,
                }
            })
        };

        fetch("https://api.airtable.com/v0/appPsBMka6L5vpHXI/brandtabs", requestOptions).then();
    }

    return (
        <Tabs mode="buttons">
            <HorizontalScroll>
                {Object.keys(tabs).map(key =>
                    <TabsItem
                        onClick={() => handleClick(key)}
                        selected={activeTab === key}
                    >
                        {tabs[key]}
                    </TabsItem>
                )}
            </HorizontalScroll>
        </Tabs>
    )
}

const App = () => {
    const allBrands = Object.keys(brands_data).filter(item => !filters.hidden.includes(item)).sort(() => Math.random() - 0.5);
    const [brands, setBrands] = React.useState(allBrands);

    return (
        <AppRoot mode="partial">
            <Template projectName="TOPBRANDS" link="https://www.ykvlv.ai/top_brands">
                <h1 color="white" style={{ marginTop: 0 }}>  </h1>
                <Text weight="regular" style={{ marginBottom: 10, fontSize: 16, lineHeight: 1.5}}>
                          ,      .
                </Text>
                <BrandForm text={"     ,    :"} placeholder={"monochrome_you"} table={"brand_suggestion"} />
                <Text weight="regular" style={{ marginBottom: 10, fontSize: 16, lineHeight: 1.5}}>
                            ,    .
                </Text>
                <BrandForm text={"     email:"} placeholder={"top_brands@ykvlv.ai"} table={"brand_analytics"} />
                <Filters setBrands={setBrands} allBrands={allBrands}/>
                {brands.map(key => <Brand key={key} brand={brands_data[key]} brand_name={key}/>)}
            </Template>
        </AppRoot>
    )
}

ReactDOM.render(
    <ConfigProvider>
        <AdaptivityProvider>
            <App />
        </AdaptivityProvider>
    </ConfigProvider>,
    document.getElementById('root')
);
